<template>
    <div id="why-we">
        <div class="container">
            <div class="row">
                <div class="col s12 m12 l6">
                    <div class="card">
                        <div class="card-image">
                            <img src="./../../../static/imgs/about-us/about-company.jpg">
                        </div>
                    </div>
                </div>
                <div class="col s12 m12 l6">
                    <h5>Why We Are Different</h5>
                    <p>It all started in elementary school when I realized that I wasn't like everyone else. My mom says that I have a tendency of obsessing on certain subjects. Unfortunately, these subjects don't interest other kids my age and they really don't interest my teachers. In fact, my kindergarten teacher said she would scream if I mentioned snakes or lizards one more time while she was teaching the days of the week.</p>
                    <blockquote>I might be different because I have different interests than other teenagers, but that doesn't give them the right to be so mean and cruel to me.</blockquote>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "WhyWeAreDiff"
}
</script>

<style scoped>
#why-we {
    padding: 30px 0 10px;
}

#why-we h5 {
    margin: 4px 0 0 0;
    font-weight: 500;
    font-size: 25px;
}

#why-we p {
    color: #32424a;
    margin-bottom: 30px
}

#why-we blockquote {
    color: #32424a
}
</style>



