<template>
    <div id="navbar">
        <nav>
            <div class="container">
                <div class="nav-wrapper">
                    <router-link to="/" class="brand-logo">
                        <img src="./../../../static/imgs/timer.png" alt="">
                    </router-link>

                    <ul id="nav-mobile" class="right hide-on-med-and-down">
                        <li><router-link to="/">Home</router-link></li>
                        <li><router-link to="/about">About</router-link></li>
                        <li><router-link to="/blog">Blog</router-link></li>
                        <li><router-link to="/contact">Conatct</router-link></li>
                    </ul>
                </div>
            </div>
            <span data-target="mobile-nav" class="top-nav sidenav-trigger full hide-on-large-only">
                <i class="material-icons">menu</i>
            </span>
        </nav>


        <ul id="mobile-nav" class="sidenav">
            <li>
                <div class="user-view">
                    <div class="background">
                        <img src="https://images.pexels.com/photos/2801312/pexels-photo-2801312.jpeg?auto=compress&cs=tinysrgb&dpr=1&w=500">
                    </div>

                    <a href="#user"><img class="circle" src="https://images.pexels.com/photos/2805877/pexels-photo-2805877.jpeg?auto=compress&cs=tinysrgb&dpr=1&w=500"></a>
                    <a href="#name"><span class="white-text name">John Doe</span></a>
                    <a href="#email"><span class="white-text email">jdandturk@gmail.com</span></a>
                </div>
            </li>
            <li><div class="divider"></div></li>
            
            <li>
                <router-link to="/" class="waves-effect"><i class="material-icons">home</i> Home</router-link>
            </li>
            <li>
                <router-link to="/about" class="waves-effect"><i class="material-icons">folder_shared</i> About</router-link>
            </li>
            <li>
                <router-link to="/blog" class="waves-effect"><i class="material-icons">settings_overscan</i> Blog</router-link>
            </li>
            <li>
                <router-link to="/contact" class="waves-effect"><i class="material-icons">local_phone</i> Contact</router-link>
            </li>
        </ul>

    </div>
</template>

<script>
export default {
    name: "Navbar",
    mounted () {
        this.$nextTick(() => {
            $('.sidenav').sidenav();
        })
    }
}
</script>

<style scoped>
#navbar nav {
    background-color: #fff;
}

#navbar nav #nav-mobile li a {
    color: #54d2e2;
    font-size: 21px;
}

.top-nav i {
    color: #54d2e2;
    font-size: 33px;
    padding-left: 15px;
    cursor: pointer;
}
</style>



