<template>
    <div id="post">

        <div class="container">
            <div class="row">
                <div class="col s12">
                    <div class="card">
                        <div class="card-image">
                            <img class="materialboxed" :src="'./../../../static/imgs/blog/' + img">
                        </div>

                        <div class="card-content">
                            <h2 class="card-title">{{ title }}</h2>
                            <p class="desc">{{ desc }}</p>
                            <blockquote class="imprtant" v-show="important">{{ important }}</blockquote>
                            <p class="desc" v-show="desc2">{{ desc2 }}</p>
                            <p class="desc" v-show="desc3">{{ desc3 }}</p>
                        </div>
                    </div>
                </div>
            </div>
        </div>


    </div>
</template>

<script>
import jsonPosts from "../../json/blog.json"
import db from "./../firebaseinit"

export default {
    name: "Post",
    data () {
        return {
            posts: jsonPosts,
            postID: null,
            title: null,
            desc: null,
            desc2: null,
            desc3: null,
            img: null,
            tags: null,
            important: null,
            date: null,
            by: null
        }
    },
    beforeRouteEnter (to, from, next) {
        db.collection('posts').where('id', '==', to.params.id).get()
        .then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                    vm.postID       = doc.data().id,
                    vm.title        = doc.data().title,
                    vm.desc         = doc.data().desc,
                    vm.desc2        = doc.data().desc2,
                    vm.desc3        = doc.data().desc3,
                    vm.img          = doc.data().img,
                    vm.tags         = doc.data().tags,
                    vm.important    = doc.data().important,
                    vm.date         = doc.data().date,
                    vm.by           = doc.data().by
                })
            })
        })
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData () {
            db.collection('posts').where('id', '==', this.$route.params.id).get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    this.postID         = doc.data().id,
                    this.title          = doc.data().title,
                    this.desc           = doc.data().desc,
                    this.desc2          = doc.data().desc2,
                    this.desc3          = doc.data().desc3,
                    this.img            = doc.data().img,
                    this.tags           = doc.data().tags,
                    this.important      = doc.data().important,
                    this.date           = doc.data().date,
                    this.by             = doc.data().by
                })
            })
        }
    },
    mounted () {
        this.$nextTick(() => {
            $('.materialboxed').materialbox();
        })
    }
}
</script>


<style scoped>
#post .card .card-title {
    margin: 0;
    font-weight: bold;
}

#post .card .h6 {
    color: #666;
    font-size: 13px;
}

#post .card .h6 span {
  color: #9a9696
}

#post .card .desc {
    margin: 0;
    margin-top: 18px;
}

</style>



