<template>
    <div id="blog">

        <PageInfo :header="header" :pageLink="pageLink" :pageName="pageName"></PageInfo>

        <div class="blog-content">
            <div class="container">
                <div class="row">
                    <div class="col s12 m12 l4">
                        <input type="text" placeholder="Select specific time.." class="datepicker">
                        
                        <ul class="collection with-header">
                            <li class="collection-header"><h4>Categories</h4></li>
                            <li class="collection-item"><div>Audio<span class="secondary-content">1</span></div></li>
                            <li class="collection-item"><div>Gallery<span class="secondary-content">2</span></div></li>
                            <li class="collection-item"><div>Image<span class="secondary-content">4</span></div></li>
                            <li class="collection-item"><div>Standers<span class="secondary-content">2</span></div></li>
                            <li class="collection-item"><div>Status<span class="secondary-content">3</span></div></li>
                        </ul>
                    </div>
                    <div class="col s12 m12 l8">
                        <div v-for="post in posts" :key="post.id">
                            <div class="card hoverable">
                                <div class="card-image">
                                    <img :src="'./../../../static/imgs/blog/' + post.img">
                                    <router-link :to="{name: 'Post', params: {id: post.postID}}" class="btn-floating halfway-fab waves-effect waves-light red"><i class="material-icons">aspect_ratio</i></router-link>
                                </div>

                                <div class="card-content">
                                    <h2 class="card-title ">{{ post.title }}</h2>
                                    <p class="h6"><span>{{ post.date }}</span> / by <span>{{ post.by }}</span> / <span>{{ post.tags }}</span></p>
                                    <p class="desc">{{ post.desc }}</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        

    </div>
</template>

<script>
import blogJson from "../../json/blog.json"
import db from "../firebaseinit"
import PageInfo from "./../global/PageInfo"

export default {
    name: "Blog",
    data () {
        return {
            infos: blogJson,
            posts: [],
            header: "Our Blog",
            pageLink: "/blog",
            pageName: "Our Blog"
        }
    },
    components: {
        PageInfo
    },
    mounted () {
        this.$nextTick(() => {
            $('.datepicker').datepicker();
        })
    },
    created () {
        db.collection('posts').get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                const data = {
                    "id": doc.id,
                    "postID": doc.data().id,
                    "title": doc.data().title,
                    "desc": doc.data().desc,
                    "desc2": doc.data().desc2,
                    "desc3": doc.data().desc3,
                    "img": doc.data().img,
                    "tags": doc.data().tags,
                    "date": doc.data().date,
                    "by": doc.data().by,
                    "important": doc.data().important,
                }

                this.posts.push(data)
            })
        })
    }
}
</script>

<style scoped>
#blog .blog-content {
    padding: 50px 0;
}

#blog .blog-content .collection .collection-header {
    padding: 15px 12px;
    margin: 0;
    font-size: 22px;
    font-weight: 500;
    border-bottom: 0
}

#blog .blog-content .collection .collection-header h4 {
    padding: 0;
}

#blog .blog-content .collection .collection-item {
    padding: 15px 12px;
    color: #607D8B;
    font-size: 16px;
}

#blog .blog-content .collection .collection-item span {
    color: #1d1d1d;
    border: 1px solid #989898;
    padding: 0px 6px;
    font-size: 13px;
    font-weight: 700;
    border-radius: 4px;
}

#blog .card .card-title {
    margin: 0;
    font-weight: bold;
}

#blog .card .h6 {
    color: #666;
    font-size: 13px;
}

#blog .card .h6 span {
  color: #9a9696
}

#blog .card .desc {
    margin: 0;
    margin-top: 18px;
}



</style>



