<template>
    <div id="page-info">
        <div class="container">
            <div class="row">
                <div class="col s12">
                    <h1>{{ header }}</h1>
                    <div>
                        <span><router-link to="/"><i class="material-icons">home</i> Home </router-link></span> <span class="slash"> / </span> <span><router-link :to="pageLink"> {{ pageName }}</router-link></span>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "PageInfo",
    props: ['header', 'pageLink', 'pageName']
}
</script>

<style scoped>
#page-info {
    text-align: center;
    background-color: #02BDD5;
    color: #fff;
    padding: 30px 0;
}

#page-info h1 {
    font-size: 52px;
    font-weight: 500;
    word-spacing: -3px;
    letter-spacing: -1px;
}

#page-info span {
    color: #fff
}

#page-info span.slash {
    margin-right: 7px;
    margin-left: 7px;
    font-size: 21px;
    font-weight: bold;
}

#page-info span a {
    color: #fff;
    text-decoration: none;
    font-size: 19px;
}

#page-info span a i {
    font-size: 40px;
    position: relative;
    top: 9px;
    right: 0px;
}
</style>



