<template>
    <div id="about">
        <PageInfo :header="header" :pageLink="pageLink" :pageName="pageName"></PageInfo>
        <WhyWeAreDiff></WhyWeAreDiff>
        <MeetTheTeam></MeetTheTeam>
    </div>
</template>

<script>
import PageInfo from "./../global/PageInfo.vue"
import WhyWeAreDiff from "./../about-us/WhyWeAreDiff.vue"
import MeetTheTeam from "./../about-us/MeetTheTeam.vue"

export default {
    name: "About",
    data () {
        return {
            header: 'About Us',
            pageLink: '/about',
            pageName: 'About'
        }
    },
    components: {
        PageInfo,
        WhyWeAreDiff,
        MeetTheTeam
    }
}
</script>



