import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/views/Home.vue'
import Blog from '@/components/views/Blog.vue'
import Post from '@/components/views/Post.vue'
import About from '@/components/views/About.vue'
import Contact from '@/components/views/Contact.vue'

Vue.use(Router)

export default new Router({
    mode: 'history',
    routes: [
        {
            path: '/',
            name: 'Home',
            component: Home
        },
        {
            path: '/blog',
            name: 'Blog',
            component: Blog
        },
        {
            path: '/post/:id',
            name: 'Post',
            component: Post
        },
        {
            path: '/about',
            name: 'About',
            component: About
        },,
        {
            path: '/contact',
            name: 'Contact',
            component: Contact
        }
    ]
})



